/* IMPORT ALL STYLES AND MIXINS HERE  */
/*  MIXIN FO FONT ATTACHING  */
/*  MIXIN FOR FONT DECORATING  */
/*  MIXIN FOR GRADIENT  */
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-size: 16px;
  font-family: "Poppins";
  font-weight: 400;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
  margin: 0 auto;
  padding: 0 10px;
  max-width: 1230px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

/* Down footer */
html,
body {
  height: 100%;
}

.wrapper {
  overflow: hidden;
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1 1 auto;
}

@font-face {
  font-family: "Ubuntu-Regular";
  src: url("../fonts/../fonts/Ubuntu-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
body {
  background: linear-gradient(270deg, #ec4484 -28.49%, #8871a7 83.02%);
  background-clip: text;
  -webkit-text-fill-color: black;
}

h1 {
  color: black;
  font-family: "Ubuntu-Regular";
  font-size: 6rem;
  font-style: normal;
  font-weight: 400;
  line-height: 130%;
  text-align: center;
}